* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'PT Sans', sans-serif;
}

.home,
.about,
.search {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
} 

.about {
  /* background-image: ; */
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 100px;
}

.search {
  /* background-image: ; */
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 100px;
}
button {
  /* Remove any custom positioning */
  position: static;
  /* Remove pseudo-element styling */
}

/* Remove button hover effect */
button::before {
  /* Remove background transition */
  transition: none;
}